import React from 'react'

import Slider from "react-slick";
import userImg from "../../images/userimg.jpg"
import dibya from "../../images/Dibya_Nath.jpg"
import malik from "../../images/Mallik_Galib_Shahriar.jpg"
import partho from "../../images/Partho_Debnath.jpg"
import rafi from "../../images/Rafi_SH.jpg"
import girl from "../../images/Girl_Common.jpg"


import FormatQuoteIcon from '@mui/icons-material/FormatQuote';
import { Avatar } from '@mui/material';
import HorizontalRuleIcon from '@mui/icons-material/HorizontalRule';

const commentsData = [
  {
    profileImgSrc: partho,
    userName: " ",
    comment: "    SQA                            ,       SQA              ,    ,        "

  },
  {
    profileImgSrc: rafi,
    userName: "",
    comment: " SQA                                   ,             -         ",

  },
  {
    profileImgSrc: girl,
    userName: " ",
    comment: " QA                     SQA             SQA          SQA    ,      SQA               ",

  },
  {
    profileImgSrc: dibya,
    userName: " ",
    comment: "SQA  ,                       SQA            ,             SQA          ",

  },
  {
    profileImgSrc: malik,
    userName: "  ",
    comment: ",   SQA        ,                       -   ,            --             ",

  },
  
  {
    profileImgSrc: girl,
    userName: "  ",
    comment: "  SQA                    ,                       SQA      ,        ",

  },
]
const settings = {
  dots: true,
      // infinite: true,
      speed: 1000,
      slidesToShow: 1,
      slidesToScroll: 1,
      lazyLoad: true,
      pauseOnDotsHover:"true", 
      pauseOnFocus: "true",
      autoplay:'true',
      adaptiveHeight:"true",
      useCSS: 'true',
      useTransform: 'true',
      appendDots: dots => (
        <div
          style={{
            padding: "2px",
            backgroundColor: "#ffffff",
            borderRadius: "5px"
          }}
        >
          <ul style={{ margin: "0px" }}> {dots} </ul>
        </div>
      ),
      customPaging: i => (
        <div
          style={{
            width: "30px",
            height: "30px",
            fontSize:"1rem",
            color: "gray",
          }}
        >
          <HorizontalRuleIcon />
        </div>
      )
      


}
const WithCarousel = () => {
  return (
    <div className='carosol' style={{marginTop: "30px"}}>
      <Slider {...settings} className='cro'  >
    {commentsData.map((comment)=> {
      return(

    <div className='carosel__div'>
      <div className='carosel__div__top'>
        <FormatQuoteIcon sx={{
          rotate: "180deg",
          color: "#F23460" ,
          fontSize: "3rem",
          // fontWeight: "700"
        }}  />
      </div>
      <div className='carosel__div__middle'>
        <p>
          {comment.comment}
        </p>
      </div>
      <div className='carosel__div__bottom' style={{marginBottom: "0"}}>
        <div className='user__profile__img'>
          {/* <img alt={'user img'} src={comment.profileImgSrc} /> */}
          <Avatar src={comment.profileImgSrc} sx={{ width: 100, height: 100 }} />
        </div>
        <div className='user__profile__name' style={{textAlign: "center"}} >
          <span>{comment.userName}</span>
        </div>
      </div>
    </div>

)
})}
</Slider>
    </div>
  )
}

export default WithCarousel